<template>
  <div>
    <el-table
      ref="singleTable"
      :data="
        tableData.slice((currentPage - 1) * pageSize, currentPage * pageSize)
      "
      highlight-current-row
      style="font-size: 16px"
      border
      :cell-style="{ textAlign: 'center' }"
      :header-cell-style="{ textAlign: 'center' }"
    >
      <el-table-column type="index" width="50"> </el-table-column>
      <el-table-column property="cover" label="封面" width="120">
        <template slot-scope="scope">
          <el-image :src="scope.row.cover" style="width: 50px; height: 50px" />
        </template>
      </el-table-column>
      <el-table-column width="120">
        <template slot-scope="scope">
          <div v-if="scope.row.recordFile" class="divs">
            <div class="audio">
              <audioplay 
              :recordFile="scope.row.recordFile"
              ></audioplay>
            </div>
          </div>
          <span v-else>-</span>
        </template>
      </el-table-column>
      <el-table-column property="musicTitle" label="音乐标题" width="120">
      </el-table-column>
      <el-table-column property="singer" label="歌手"> </el-table-column>
      <el-table-column property="album" label="专辑"> </el-table-column>
      <el-table-column property="duration" label="时长"> </el-table-column>
    </el-table>
    <br />
    <el-pagination
      align="center"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[1, 5, 10, 20]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="tableData.length"
    >
    </el-pagination>
  </div>
</template>
<!-- :page-sizes的值表示可以选择一页多少条；
:page-size的值表示当前一页显示几条 -->
<script>
import audioplay from "@/components/audioplay";
export default {
  name: "LatestMusic",
  components:{
    audioplay:audioplay,
  },
  data() {
    return {
      tableHead: [
        {
          column_name: "cover",
          column_comment: "封面"
        },
        {
          column_name: "musicTitle",
          column_comment: "音乐标题"
        },
        {
          column_name: "singer",
          column_comment: "歌手"
        },
        {
          column_name: "album",
          column_comment: "专辑"
        },
        {
          column_name: "duration",
          column_comment: "时长"
        }
      ],
      tableData: [
        {
          cover: require("../assets/龙珠.jpg"),
          musicTitle: "王小虎",
          singer: "上海市普陀区金沙江路 1518 弄",
          album: "sdas",
          duration: "dsa",
          recordFile:require("../assets/music.mp3"),
        },
        {
          musicTitle: "王小虎",
          singer: "上海市普陀区金沙江路 1518 弄",
          album: "sdas",
          duration: "dsa",
        },
        {
          musicTitle: "王小虎",
          singer: "上海市普陀区金沙江路 1518 弄",
          album: "sdas",
          duration: "dsa"
        }
      ],
      currentPage: 1, // 当前页码
      total: 20, // 总条数
      pageSize: 2, // 每页的数据条数
    };
  },
  methods: {
    //每页条数改变时触发 选择一页显示多少行
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.currentPage = 1;
      this.pageSize = val;
    },
    //当前页改变时触发 跳转其他页
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
    }
  }
  //函数名称是自己定义的，可以随便修改，但是记住，要与<el-pagination>标签中的@size-change 和 @current-change 保持一致
};
</script>

<style scoped>

</style>



